export function friendlyDate(datsStr) {
  let dateObj = typeof datsStr === 'object' ? datsStr : new Date(datsStr)
  let time = dateObj.getTime()
  let now = Date.now()
  let space = now - time
  let str = ''

  switch (true) {
    case space < 60000:
      str = 'åˆšåˆš'
      break
    case space < 1000*3600:
      str = Math.floor(space/60000) + 'åˆ†é’Ÿå‰'
      break
    case space < 1000*3600*24:
      str = Math.floor(space/(1000*3600)) + 'å°æ—¶å‰'
      break
    default:
      str = Math.floor(space/(1000*3600*24)) + 'å¤©å‰'
  }
  return str
}